<h1 id="消息" tabindex="-1"><a class="header-anchor" href="#消息" aria-hidden="true">#</a> 消息</h1>
<h2 id="主动发送消息" tabindex="-1"><a class="header-anchor" href="#主动发送消息" aria-hidden="true">#</a> 主动发送消息</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token keyword">use</span> <span class="token package">EasyWeChat<span class="token punctuation">\</span>Kernel<span class="token punctuation">\</span>Messages<span class="token punctuation">\</span>TextCard</span><span class="token punctuation">;</span>


<span class="token comment">// 获取 Messenger 实例</span>
<span class="token variable">$messenger</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">messenger</span><span class="token punctuation">;</span>

<span class="token comment">// 准备消息</span>
<span class="token variable">$message</span> <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">TextCard</span><span class="token punctuation">(</span><span class="token punctuation">[</span>
    <span class="token string single-quoted-string">'title'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'你的请假单审批通过'</span><span class="token punctuation">,</span> 
    <span class="token string single-quoted-string">'description'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'单号：1928373, ....'</span><span class="token punctuation">,</span> 
    <span class="token string single-quoted-string">'url'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'http://easywechat.com/oa/....'</span>
<span class="token punctuation">]</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// 发送</span>
<span class="token variable">$messenger</span><span class="token operator">-></span><span class="token function">message</span><span class="token punctuation">(</span><span class="token variable">$message</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">toUser</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'overtrue'</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">send</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br></div></div><p>你也可以很方便的发送普通文本消息：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$messenger</span><span class="token operator">-></span><span class="token function">message</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'你的请假单（单号：1928373）已经审批通过！'</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">toUser</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'overtrue'</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">send</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token comment">// 或者写成</span>
<span class="token variable">$messenger</span><span class="token operator">-></span><span class="token function">toUser</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'overtrue'</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">send</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'你的请假单（单号：1928373）已经审批通过！'</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><h2 id="接收消息" tabindex="-1"><a class="header-anchor" href="#接收消息" aria-hidden="true">#</a> 接收消息</h2>
<p>被动接收消息，与回复消息，请参考：<a href="server">服务端</a></p>
<h2 id="更新任务卡片消息状态" tabindex="-1"><a class="header-anchor" href="#更新任务卡片消息状态" aria-hidden="true">#</a> 更新任务卡片消息状态</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$messenger</span><span class="token operator">-></span><span class="token function">updateTaskcard</span><span class="token punctuation">(</span><span class="token keyword type-hint">array</span> <span class="token variable">$userids</span><span class="token punctuation">,</span> <span class="token keyword type-hint">int</span> <span class="token variable">$agentId</span><span class="token punctuation">,</span> <span class="token keyword type-hint">string</span> <span class="token variable">$taskId</span><span class="token punctuation">,</span> <span class="token keyword type-hint">string</span> <span class="token variable">$replaceName</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'已收到'</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div>